.heading {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  min-width: 400px;
  max-width: 100%;
}
.button,
.graphBuilder {
  position: relative;
  line-height: 150%;
}
.graphBuilder {
  align-self: stretch;
}
.button {
  display: inline-block;
  min-width: 53px;
}
.styleprimarySmallfalseDa {
  background-color: var(--border-primary);
  border: 1px solid var(--border-primary);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-xs) var(--padding-4xl);
}
.button1 {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 63px;
}
.stylesecondarySmallfalse {
  border: 1px solid var(--border-primary);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-xs) var(--padding-4xl);
  color: var(--border-primary);
}
.actions,
.container,
.content,
.cta13 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.actions {
  flex-direction: row;
  padding: var(--padding-base) 0 0;
  gap: var(--gap-base);
  font-size: var(--text-regular-normal-size);
  color: var(--background-color-primary);
}
.container,
.content,
.cta13 {
  max-width: 100%;
}
.content {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-5xl);
  min-width: 400px;
  font-size: var(--text-medium-bold-size);
}
.container,
.cta13 {
  align-self: stretch;
}
.container {
  flex-direction: row;
  flex-wrap: wrap;
  gap: var(--gap-61xl);
}
.cta13 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--heading-desktop-h2-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 825px) {
  .heading {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .content,
  .heading {
    min-width: 100%;
  }
  .container {
    gap: var(--gap-21xl);
  }
  .cta13 {
    gap: 40px 80px;
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .container {
    gap: var(--gap-xl);
  }
  .cta13 {
    gap: 20px 80px;
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
