.heading {
  width: 320px;
  position: relative;
  line-height: 150%;
  display: inline-block;
  flex-shrink: 0;
}
.logo1ColordarkIcon,
.logo1ColordarkIcon1,
.logo2ColordarkIcon,
.logo2ColordarkIcon1,
.logo2ColordarkIcon2 {
  height: 56px;
  width: 140px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 56px;
}
.container,
.content,
.logo2 {
  display: flex;
  align-items: center;
  max-width: 100%;
}
.content {
  flex: 1;
  flex-direction: row;
  justify-content: center;
  padding: 0 0 0 var(--padding-81xl);
  box-sizing: border-box;
  gap: var(--gap-13xl);
  min-width: 603px;
}
.container,
.logo2 {
  align-self: stretch;
}
.container {
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: var(--gap-45xl);
}
.logo2 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  flex-direction: column;
  justify-content: center;
  padding: var(--padding-61xl) var(--padding-45xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--text-medium-bold-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .content {
    flex-wrap: wrap;
    padding-left: var(--padding-31xl);
    box-sizing: border-box;
    min-width: 100%;
  }
}
@media screen and (max-width: 750px) {
  .container {
    gap: var(--gap-13xl);
  }
  .logo2 {
    gap: 40px 80px;
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .content {
    gap: 32px 16px;
    padding-left: var(--padding-6xl);
    box-sizing: border-box;
  }
  .container {
    gap: var(--gap-base);
  }
  .logo2 {
    gap: 20px 80px;
  }
}
