.faqs,
.text {
  align-self: stretch;
  position: relative;
}
.faqs {
  margin: 0;
  font-size: inherit;
  line-height: 58px;
  font-weight: 700;
  font-family: inherit;
}
.text {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.sectionTitle {
  width: 768px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  max-width: 100%;
}
.divider {
  width: 1312px;
  height: 1px;
  position: relative;
  background-color: var(--border-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
}
.accordion {
  width: 1312px;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.stillHaveA {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 42px;
  font-weight: 700;
  font-family: inherit;
}
.content1,
.stillHaveA,
.text1 {
  align-self: stretch;
}
.text1 {
  position: relative;
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
  flex-shrink: 0;
}
.button {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 56px;
}
.content,
.faq2,
.stylesecondarySmallfalse {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.stylesecondarySmallfalse {
  height: 50px;
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  flex-direction: row;
  padding: var(--padding-xs) var(--padding-5xl);
  font-size: var(--text-regular-normal-size);
}
.content,
.faq2 {
  flex-direction: column;
  max-width: 100%;
}
.content {
  width: 560px;
  gap: var(--gap-5xl);
  font-size: var(--heading-desktop-h4-size);
}
.faq2 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  gap: var(--gap-61xl);
  text-align: left;
  font-size: var(--heading-desktop-h2-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .faqs {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .stillHaveA {
    font-size: var(--font-size-7xl);
    line-height: 33px;
  }
  .faq2 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .faq2 {
    gap: 40px 80px;
    padding: var(--padding-28xl) var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .faqs {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .stillHaveA {
    font-size: var(--font-size-lgi);
    line-height: 25px;
  }
  .faq2 {
    gap: 20px 80px;
  }
}
