.tagline {
  width: 63px;
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 63px;
}
.loremIpsumDolor,
.shortHeadingHere {
  align-self: stretch;
  position: relative;
}
.shortHeadingHere {
  margin: 0;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
}
.loremIpsumDolor {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content,
.sectionTitle {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content {
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--heading-desktop-h1-size);
}
.sectionTitle {
  gap: var(--gap-base);
}
.button {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 35px;
}
.styleprimarySmallfalseDa {
  width: 83px;
  background-color: var(--border-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-xs) var(--padding-4xl);
}
.button1 {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 81px;
  white-space: nowrap;
}
.stylesecondarySmallfalse {
  border: 1px solid var(--border-primary);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-xs) var(--padding-4xl);
  color: var(--border-primary);
}
.actions,
.container,
.header44 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.actions {
  flex-direction: row;
  padding: var(--padding-base) 0 0;
  gap: var(--gap-base);
  text-align: left;
  color: var(--background-color-primary);
}
.container,
.header44 {
  flex-direction: column;
  max-width: 100%;
}
.container {
  width: 768px;
  gap: var(--gap-5xl);
}
.header44 {
  align-self: stretch;
  background-color: var(--background-color-primary);
  overflow: hidden;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .header44 {
    gap: 40px 80px;
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .shortHeadingHere {
    font-size: var(--font-size-26xl);
    line-height: 54px;
  }
  .header44 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .shortHeadingHere {
    font-size: var(--font-size-15xl);
    line-height: 40px;
  }
  .header44 {
    gap: 20px 80px;
  }
}
