.placeholderImageIcon {
  align-self: stretch;
  height: 300px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.symbolMasking {
  position: relative;
  font-size: var(--text-small-semi-bold-size);
  line-height: 150%;
  font-weight: 600;
  font-family: var(--text-medium-bold);
  color: var(--border-primary);
  text-align: left;
  display: inline-block;
  min-width: 58px;
}
.container {
  cursor: pointer;
  border: 0;
  padding: var(--padding-9xs) var(--padding-6xs);
  background-color: var(--color-neutral-neutral-lightest);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.text {
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 67px;
}
.info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.heading,
.symbolBlending {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  line-height: 140%;
  font-weight: 700;
  font-family: inherit;
}
.symbolBlending {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.content1,
.content2 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content2 {
  gap: var(--gap-5xs);
  font-size: var(--heading-desktop-h5-size);
}
.content1 {
  gap: var(--gap-base);
}
.button {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 77px;
}
.iconChevronRight {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 24px;
}
.stylelinkSmallfalseDark {
  height: 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
  font-size: var(--text-regular-normal-size);
}
.card,
.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content {
  align-self: stretch;
  padding: var(--padding-5xl);
  gap: var(--gap-5xl);
}
.card {
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  max-width: 100%;
  flex-shrink: 0;
  text-align: left;
  font-size: var(--text-small-semi-bold-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-lgi);
    line-height: 27px;
  }
}
