.subheading {
  width: 105px;
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 105px;
}
.heading,
.mergeMatrices {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
}
.mergeMatrices {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content1,
.sectionTitle {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content1 {
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--heading-desktop-h2-size);
}
.sectionTitle {
  gap: var(--gap-base);
}
.button {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 68px;
}
.iconChevronRight {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 24px;
}
.stylelinkSmallfalseDark {
  height: 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.actions,
.content {
  display: flex;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.actions {
  flex-direction: row;
  align-items: center;
  padding: var(--padding-base) 0 0;
  text-align: left;
}
.content {
  align-self: stretch;
  flex-direction: column;
  align-items: flex-start;
  text-align: center;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .heading {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
}
