.subheading {
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 63px;
}
.heading {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--heading-desktop-h2-size);
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
}
.column,
.content1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content1 {
  align-self: stretch;
  gap: var(--gap-base);
}
.column {
  flex: 1;
  gap: var(--gap-13xl);
  min-width: 400px;
  max-width: 100%;
}
.iconRelume {
  width: 48px;
  height: 48px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.content2 {
  height: 48px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.feedUpdater,
.heading1 {
  align-self: stretch;
  position: relative;
  line-height: 140%;
}
.feedUpdater {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.content3 {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
  min-width: 343px;
  max-width: 100%;
}
.timelineItem {
  align-self: stretch;
  flex-direction: row;
  gap: var(--gap-21xl);
}
.content,
.layout121,
.timeline,
.timelineItem {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.timeline {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-base);
  min-width: 400px;
  font-size: var(--heading-desktop-h6-size);
}
.content,
.layout121 {
  flex-direction: row;
}
.content {
  flex: 1;
  flex-wrap: wrap;
  gap: var(--gap-61xl);
}
.layout121 {
  align-self: stretch;
  background-color: var(--background-color-primary);
  overflow: hidden;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .heading {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .layout121 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .column {
    gap: 16px 32px;
  }
  .column,
  .content3 {
    min-width: 100%;
  }
  .timelineItem {
    flex-wrap: wrap;
    gap: 40px 20px;
  }
  .timeline {
    min-width: 100%;
  }
  .content {
    gap: var(--gap-21xl);
  }
  .layout121 {
    gap: 80px 40px;
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .heading1 {
    font-size: var(--text-regular-normal-size);
    line-height: 22px;
  }
  .content {
    gap: var(--gap-xl);
  }
  .layout121 {
    gap: 80px 20px;
    padding-top: var(--padding-28xl);
    padding-bottom: var(--padding-28xl);
    box-sizing: border-box;
  }
}
