.alignmentGuideIcon,
.shapeRotatorIcon,
.symbolScalerIcon,
.vectorIcon,
.vectorIcon1 {
  height: 18.9px;
  width: 20px;
  position: relative;
  min-height: 19px;
}
.stars {
  height: 18.9px;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.quote {
  align-self: stretch;
  position: relative;
  line-height: 140%;
}
.avatarImageIcon {
  width: 56px;
  height: 56px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
}
.avatarImageWrapper {
  height: 58.5px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-10xs-5) 0 0;
  box-sizing: border-box;
}
.functionCall {
  font-weight: 600;
  display: inline-block;
  min-width: 68px;
}
.functionCall,
.variableStorage {
  position: relative;
  line-height: 150%;
}
.avatarContent,
.iterativeLoop {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.iterativeLoop {
  padding: var(--padding-6xs-5) 0 0;
}
.divider {
  height: 62px;
  width: 1px;
  position: relative;
  border-right: 1px solid var(--border-primary);
  box-sizing: border-box;
}
.logo2ColordarkIcon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.avatar,
.container,
.logo2ColordarkWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.logo2ColordarkWrapper {
  height: 58.5px;
  width: 140px;
  flex-direction: column;
  padding: var(--padding-10xs-5) 0 0;
  box-sizing: border-box;
}
.avatar,
.container {
  flex-direction: row;
  gap: var(--gap-lgi-7);
  max-width: 100%;
  font-size: var(--text-regular-normal-size);
}
.container {
  flex: 1;
  overflow: hidden;
  flex-direction: column;
  gap: var(--gap-13xl);
  min-width: 406px;
  text-align: left;
  font-size: var(--heading-desktop-h6-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 750px) {
  .divider {
    width: 100%;
    height: 1px;
  }
  .avatar {
    flex-wrap: wrap;
  }
  .container {
    gap: 16px 32px;
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .quote {
    font-size: var(--text-regular-normal-size);
    line-height: 22px;
  }
}
