.icon {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.button {
  width: 48px;
  height: 48px;
  border-radius: var(--br-31xl);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-xs) var(--padding-2xs);
}
.buttonWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-79xl-9) 0 0;
}
.vectorIcon,
.vectorIcon1,
.vectorIcon2,
.vectorIcon3,
.vectorIcon4 {
  height: 18.9px;
  width: 20px;
  position: relative;
  min-height: 19px;
}
.dataAggregator,
.stars {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.stars {
  height: 18.9px;
  overflow: hidden;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.dataAggregator {
  align-self: stretch;
  justify-content: center;
  padding: 0 var(--padding-xl);
}
.quote {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 140%;
  font-weight: 700;
  font-family: inherit;
}
.avatarImageIcon {
  width: 56px;
  height: 56px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
}
.avatarImageWrapper {
  height: 58.5px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-10xs-5) 0 0;
  box-sizing: border-box;
}
.text {
  font-weight: 600;
  display: inline-block;
  min-width: 68px;
}
.text,
.text1 {
  position: relative;
  line-height: 150%;
}
.avatarContent,
.avatarContentWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.avatarContentWrapper {
  flex: 1;
  padding: var(--padding-6xs-5) 0 0;
  box-sizing: border-box;
  min-width: 91px;
  flex-shrink: 0;
}
.divider {
  height: 62px;
  width: 1px;
  position: relative;
  border-right: 1px solid var(--border-primary);
  box-sizing: border-box;
}
.logo2ColordarkIcon {
  align-self: stretch;
  height: 56px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
}
.logo2ColordarkWrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-10xs-5) 0 0;
  box-sizing: border-box;
  min-width: 91px;
  flex-shrink: 0;
}
.avatar,
.avatarWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  max-width: 100%;
}
.avatar {
  width: 396px;
  gap: var(--gap-lgi-7);
}
.avatarWrapper {
  align-self: stretch;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--text-regular-normal-size);
}
.container1,
.icon1 {
  overflow: hidden;
  flex-shrink: 0;
}
.container1 {
  width: 768px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-13xl);
  max-width: 100%;
}
.icon1 {
  height: 24px;
  width: 24px;
  position: relative;
}
.button1 {
  width: 48px;
  height: 48px;
  border-radius: var(--br-31xl);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-xs) var(--padding-2xs);
}
.buttonContainer,
.content {
  display: flex;
  align-items: flex-start;
}
.buttonContainer {
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-79xl-9) 0 0;
}
.content {
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100%;
  gap: var(--gap-xl);
}
.dot,
.dot1,
.dot2,
.dot3,
.dot4 {
  height: 8px;
  width: 8px;
  position: relative;
  border-radius: 50%;
  background-color: var(--border-primary);
}
.dot1,
.dot2,
.dot3,
.dot4 {
  background-color: var(--color-neutral-neutral-lighter);
}
.progressBar,
.sliderDots {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.sliderDots {
  justify-content: flex-start;
  padding: var(--padding-3xs);
  gap: var(--gap-4xs);
}
.progressBar {
  align-self: stretch;
  justify-content: center;
  padding: 0 var(--padding-xl);
}
.container,
.testimonial10,
.testimonial10Wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.container {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-29xl);
}
.testimonial10,
.testimonial10Wrapper {
  flex-direction: row;
  box-sizing: border-box;
}
.testimonial10 {
  flex: 1;
  background-color: var(--background-color-primary);
  overflow: hidden;
  padding: var(--padding-93xl) var(--padding-45xl);
}
.testimonial10Wrapper {
  align-self: stretch;
  padding: 0 0 var(--padding-605xl);
  text-align: center;
  font-size: var(--heading-desktop-h5-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1200px) {
  .testimonial10Wrapper {
    padding-bottom: var(--padding-387xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .content {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .container {
    gap: 24px 48px;
  }
  .testimonial10 {
    gap: 80px 40px;
    padding: var(--padding-54xl) var(--padding-13xl);
    box-sizing: border-box;
  }
  .testimonial10Wrapper {
    padding-bottom: var(--padding-245xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .quote {
    font-size: var(--font-size-lgi);
    line-height: 27px;
  }
  .divider {
    width: 100%;
    height: 1px;
  }
  .avatar {
    flex-wrap: wrap;
  }
  .container1 {
    gap: 16px 32px;
  }
  .testimonial10 {
    gap: 80px 20px;
  }
}
