.placeholderImageIcon {
  align-self: stretch;
  height: 300px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.text {
  line-height: 150%;
  font-weight: 600;
}
.heading,
.shapeToolsPanel,
.text {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: var(--heading-desktop-h5-size);
  line-height: 140%;
  font-weight: 700;
  font-family: inherit;
}
.shapeToolsPanel {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.content1 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.placeholderImageIcon1 {
  height: 48px;
  width: 48px;
  position: relative;
  object-fit: cover;
  min-height: 48px;
}
.text1 {
  align-self: stretch;
  position: relative;
  line-height: 150%;
  font-weight: 600;
}
.dataAggregator {
  min-width: 78px;
}
.dataAggregator,
.div,
.inputFilter {
  position: relative;
  line-height: 150%;
  display: inline-block;
}
.div {
  font-size: var(--text-medium-bold-size);
  min-width: 7px;
}
.inputFilter {
  min-width: 66px;
}
.time {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-118xl) 0 0;
  gap: var(--gap-5xs);
}
.content2 {
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  min-width: 198px;
}
.avatar,
.card,
.content,
.content2 {
  display: flex;
  justify-content: flex-start;
}
.avatar {
  align-self: stretch;
  flex-direction: row;
  align-items: center;
  gap: var(--gap-base);
}
.card,
.content {
  flex-direction: column;
  align-items: flex-start;
}
.content {
  align-self: stretch;
  padding: var(--padding-5xl);
  gap: var(--gap-5xl);
}
.card {
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  max-width: 100%;
  text-align: left;
  font-size: var(--text-small-semi-bold-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-lgi);
    line-height: 27px;
  }
  .div {
    width: 100%;
    height: 7px;
  }
  .time {
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .avatar,
  .time {
    flex-wrap: wrap;
  }
}
