.subheading {
  line-height: 150%;
  font-weight: 600;
}
.heading,
.subheading,
.text {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  line-height: 58px;
  font-weight: 700;
  font-family: inherit;
}
.text {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.headingParent,
.matrixAdditionParent {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.headingParent {
  flex-direction: column;
  gap: var(--gap-5xl);
  font-size: var(--heading-desktop-h2-size);
}
.matrixAdditionParent {
  flex-direction: row;
  flex-wrap: wrap;
  gap: 64px 30.7px;
  min-height: 1110px;
  font-size: var(--text-medium-bold-size);
}
.stylesecondarySmallfalse {
  height: 50px;
}
.product4,
.stylesecondarySmallfalseWrapper {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
}
.stylesecondarySmallfalseWrapper {
  height: 48px;
  flex-direction: row;
  justify-content: center;
}
.product4 {
  background-color: var(--color-mediumorchid);
  overflow: hidden;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-93xl) var(--padding-45xl);
  gap: var(--gap-45xl);
  text-align: center;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1200px) {
  .product4 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .heading {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .product4 {
    padding-top: var(--padding-28xl);
    padding-bottom: var(--padding-28xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .matrixAdditionParent {
    gap: 64px 15px;
  }
  .product4 {
    gap: 32px 64px;
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .product4 {
    gap: 16px 64px;
    padding-top: var(--padding-12xl);
    padding-bottom: var(--padding-12xl);
    box-sizing: border-box;
  }
}
