.button {
  position: relative;
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
  font-family: var(--text-medium-bold);
  color: var(--border-primary);
  text-align: left;
  display: inline-block;
  min-width: 37px;
}
.stylesecondarySmallfalse {
  cursor: pointer;
  border: 1px solid var(--border-primary);
  padding: var(--padding-xs) var(--padding-4xl);
  background-color: transparent;
  width: 85px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.button1 {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 81px;
  white-space: nowrap;
}
.iconChevronRight {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 24px;
}
.actions,
.stylelinkSmallfalseDark {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.stylelinkSmallfalseDark {
  height: 24px;
  justify-content: center;
  gap: var(--gap-5xs);
}
.actions {
  justify-content: flex-start;
  padding: var(--padding-base) 0 0;
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
