.subheading {
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 56px;
}
.heading,
.text {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  line-height: 58px;
  font-weight: 700;
  font-family: inherit;
}
.text {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content,
.sectionTitle {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content {
  align-self: stretch;
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--heading-desktop-h2-size);
}
.sectionTitle {
  width: 768px;
  gap: var(--gap-base);
  max-width: 100%;
}
.iconMap {
  width: 32px;
  height: 32px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.heading1 {
  align-self: stretch;
  position: relative;
  font-size: var(--heading-desktop-h6-size);
  line-height: 140%;
}
.button,
.loopProcessor {
  position: relative;
  line-height: 150%;
}
.loopProcessor {
  align-self: stretch;
}
.button {
  display: inline-block;
  min-width: 101px;
}
.iconChevronRight {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 24px;
}
.stylelinkSmallfalseDark {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.actions {
  align-self: stretch;
  height: 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) 0 0;
  box-sizing: border-box;
}
.contactInfo {
  align-self: stretch;
  gap: var(--gap-5xs);
}
.contactInfo,
.content2,
.row {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content2 {
  align-self: stretch;
  gap: var(--gap-base);
}
.row {
  width: 400px;
  gap: var(--gap-21xl);
  min-width: 400px;
  max-width: 100%;
}
.placeholderImageIcon {
  height: 516px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  min-width: 541px;
}
.contact14,
.content1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-61xl);
  max-width: 100%;
  text-align: left;
}
.contact14 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  z-index: 1;
  text-align: center;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1200px) {
  .row {
    flex: 1;
  }
  .content1 {
    flex-wrap: wrap;
  }
  .contact14 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 825px) {
  .heading {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .placeholderImageIcon,
  .row {
    min-width: 100%;
  }
  .content1 {
    gap: 80px 40px;
  }
  .contact14 {
    gap: 40px 80px;
    padding: var(--padding-28xl) var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .heading1 {
    font-size: var(--text-regular-normal-size);
    line-height: 22px;
  }
  .row {
    gap: 20px 40px;
  }
  .content1 {
    gap: 80px 20px;
  }
  .contact14 {
    gap: 20px 80px;
  }
}
