.border {
  height: 132px;
  width: 2px;
  position: relative;
  border-right: 2px solid var(--border-primary);
  box-sizing: border-box;
}
.shortHeadingGoes,
.text {
  align-self: stretch;
  position: relative;
}
.shortHeadingGoes {
  margin: 0;
  font-size: inherit;
  line-height: 42px;
  font-weight: 700;
  font-family: inherit;
}
.text {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.content,
.tabHorizontal {
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.content {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-base);
  min-width: 380px;
  flex-shrink: 0;
}
.tabHorizontal {
  align-self: stretch;
  flex-direction: row;
  gap: var(--gap-11xl);
}
.border1 {
  height: 108px;
  width: 2px;
  position: relative;
  border-right: 2px solid var(--border-primary);
  box-sizing: border-box;
  opacity: 0;
}
.shortHeadingGoes1,
.text1 {
  align-self: stretch;
  position: relative;
}
.shortHeadingGoes1 {
  margin: 0;
  font-size: inherit;
  line-height: 42px;
  font-weight: 700;
  font-family: inherit;
}
.text1 {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.content1,
.tabHorizontal1 {
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.content1 {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-base);
  min-width: 380px;
  flex-shrink: 0;
}
.tabHorizontal1 {
  align-self: stretch;
  flex-direction: row;
  gap: var(--gap-11xl);
}
.border2 {
  height: 108px;
  width: 2px;
  position: relative;
  border-right: 2px solid var(--border-primary);
  box-sizing: border-box;
  opacity: 0;
}
.shortHeadingGoes2,
.text2 {
  align-self: stretch;
  position: relative;
}
.shortHeadingGoes2 {
  margin: 0;
  font-size: inherit;
  line-height: 42px;
  font-weight: 700;
  font-family: inherit;
}
.text2 {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.content2 {
  flex: 1;
  overflow: hidden;
  flex-direction: column;
  gap: var(--gap-base);
  min-width: 380px;
  flex-shrink: 0;
}
.content2,
.tabHorizontal2,
.tabsMenu,
.tabsMenuWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.tabHorizontal2 {
  align-self: stretch;
  overflow: hidden;
  flex-direction: row;
  gap: var(--gap-11xl);
}
.tabsMenu,
.tabsMenuWrapper {
  flex-direction: column;
}
.tabsMenu {
  align-self: stretch;
  gap: var(--gap-21xl);
}
.tabsMenuWrapper {
  flex: 1;
  padding: var(--padding-90xl) 0 0;
  box-sizing: border-box;
  min-width: 400px;
}
.placeholderImageIcon {
  height: 640px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  min-width: 400px;
}
.container,
.layout28 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.container {
  flex: 1;
  flex-wrap: wrap;
  gap: var(--gap-61xl);
}
.layout28 {
  align-self: stretch;
  background-color: var(--background-color-primary);
  overflow: hidden;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--heading-desktop-h4-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1200px) {
  .layout28 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 825px) {
  .border {
    width: 100%;
    height: 2px;
  }
  .shortHeadingGoes {
    font-size: var(--font-size-7xl);
    line-height: 33px;
  }
  .content {
    min-width: 100%;
  }
  .tabHorizontal {
    flex-wrap: wrap;
  }
  .border1 {
    width: 100%;
    height: 2px;
  }
  .shortHeadingGoes1 {
    font-size: var(--font-size-7xl);
    line-height: 33px;
  }
  .content1 {
    min-width: 100%;
  }
  .tabHorizontal1 {
    flex-wrap: wrap;
  }
  .border2 {
    width: 100%;
    height: 2px;
  }
  .shortHeadingGoes2 {
    font-size: var(--font-size-7xl);
    line-height: 33px;
  }
  .content2 {
    min-width: 100%;
  }
  .tabHorizontal2 {
    flex-wrap: wrap;
  }
  .tabsMenu {
    gap: 20px 40px;
  }
  .tabsMenuWrapper {
    padding-top: var(--padding-52xl);
    box-sizing: border-box;
    min-width: 100%;
  }
  .placeholderImageIcon {
    min-width: 100%;
  }
  .container {
    gap: var(--gap-21xl);
  }
  .layout28 {
    gap: 80px 40px;
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .shortHeadingGoes,
  .shortHeadingGoes1,
  .shortHeadingGoes2 {
    font-size: var(--font-size-lgi);
    line-height: 25px;
  }
  .container {
    gap: var(--gap-xl);
  }
  .layout28 {
    gap: 80px 20px;
    padding-top: var(--padding-28xl);
    padding-bottom: var(--padding-28xl);
    box-sizing: border-box;
  }
}
