.iconRelume {
  width: 48px;
  height: 48px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.divider {
  height: 102px;
  width: 2px;
  position: relative;
  background-color: var(--border-primary);
  border-right: 2px solid var(--border-primary);
  box-sizing: border-box;
}
.splitSplitter {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 var(--padding-3xl) 0 var(--padding-5xl);
}
.content {
  height: 164px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.checkpoint,
.heading {
  align-self: stretch;
  position: relative;
  line-height: 140%;
}
.checkpoint {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.content1,
.timelineItem {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.content1 {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-base);
  min-width: 343px;
}
.timelineItem {
  align-self: stretch;
  flex-direction: row;
  gap: var(--gap-21xl);
  text-align: left;
  font-size: var(--heading-desktop-h6-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 750px) {
  .content1 {
    min-width: 100%;
  }
  .timelineItem {
    flex-wrap: wrap;
    gap: 40px 20px;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--text-regular-normal-size);
    line-height: 22px;
  }
}
