.subheading {
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 57px;
}
.heading,
.xML {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
}
.xML {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content,
.sectionTitle {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.content {
  align-self: stretch;
  align-items: center;
  gap: var(--gap-5xl);
  font-size: var(--heading-desktop-h2-size);
}
.sectionTitle {
  width: 768px;
  align-items: flex-start;
  gap: var(--gap-base);
  max-width: 100%;
}
.button {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 57px;
}
.stylesecondarySmallfalse {
  height: 50px;
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-xs) var(--padding-4xl-5);
}
.blog42,
.row,
.title {
  align-self: stretch;
  display: flex;
  max-width: 100%;
}
.title {
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.blog42,
.row {
  align-items: flex-start;
  justify-content: flex-start;
}
.row {
  flex-direction: row;
  flex-wrap: wrap;
  gap: var(--gap-13xl);
}
.blog42 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  flex-direction: column;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  gap: var(--gap-61xl);
  text-align: left;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .title {
    flex-wrap: wrap;
  }
  .blog42 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .heading {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .row {
    gap: var(--gap-base);
  }
  .blog42 {
    gap: 40px 80px;
    padding: var(--padding-28xl) var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .blog42 {
    gap: 20px 80px;
  }
}
