.iconRelume {
  width: 48px;
  height: 48px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.subheadingOne,
.text {
  align-self: stretch;
  position: relative;
  line-height: 140%;
}
.text {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.listItem {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
  min-width: 192px;
  text-align: left;
  font-size: var(--heading-desktop-h6-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 450px) {
  .subheadingOne {
    font-size: var(--text-regular-normal-size);
    line-height: 22px;
  }
}
