.logo2ColordarkIcon {
  width: 140px;
  height: 56px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.quote {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 140%;
  font-weight: 700;
  font-family: inherit;
}
.avatarImageIcon {
  width: 56px;
  height: 56px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
}
.dataFilter {
  font-weight: 600;
}
.dataFilter,
.dataFilter1 {
  align-self: stretch;
  position: relative;
  line-height: 150%;
}
.avatar,
.avatarContent,
.content,
.testimonial1 {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.avatarContent {
  align-self: stretch;
  align-items: flex-start;
}
.avatar,
.content,
.testimonial1 {
  align-items: center;
}
.avatar {
  width: 300px;
  height: 120px;
  gap: var(--gap-base);
  font-size: var(--text-regular-normal-size);
}
.content,
.testimonial1 {
  overflow: hidden;
  max-width: 100%;
}
.content {
  width: 768px;
  gap: var(--gap-13xl);
}
.testimonial1 {
  align-self: stretch;
  background-color: var(--background-color-primary);
  padding: var(--padding-93xl) var(--padding-xl);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--heading-desktop-h5-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 825px) {
  .testimonial1 {
    gap: 40px 80px;
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .quote {
    font-size: var(--font-size-lgi);
    line-height: 27px;
  }
  .content {
    gap: 16px 32px;
  }
  .testimonial1 {
    gap: 20px 80px;
  }
}
