.placeholderImageIcon {
  height: 240px;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.heading,
.pathSplitter,
.placeholderImageIcon {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  line-height: 130%;
  font-weight: 700;
  font-family: inherit;
}
.pathSplitter {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.column,
.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content {
  align-self: stretch;
  gap: var(--gap-5xl);
}
.column {
  flex: 1;
  overflow: hidden;
  gap: var(--gap-13xl);
  min-width: 304px;
  max-width: 100%;
  text-align: left;
  font-size: var(--heading-desktop-h4-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .heading {
    font-size: var(--font-size-7xl);
    line-height: 33px;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-lgi);
    line-height: 25px;
  }
  .column {
    gap: 16px 32px;
  }
}
