.iconRelume {
  width: 48px;
  height: 48px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.heading {
  width: 95px;
  line-height: 140%;
  display: inline-block;
  min-width: 95px;
}
.heading,
.price1,
.price2 {
  position: relative;
}
.price1 {
  flex: 1;
  line-height: 120%;
}
.price2 {
  height: 42px;
  width: 0;
  font-size: var(--heading-desktop-h4-size);
  line-height: 130%;
  display: inline-block;
}
.container,
.content,
.price {
  align-self: stretch;
  display: flex;
  justify-content: flex-start;
}
.container {
  flex-direction: row;
  align-items: flex-end;
  font-size: var(--heading-desktop-h1-size);
}
.content,
.price {
  flex-direction: column;
}
.price {
  align-items: flex-start;
  padding: 0 var(--padding-215xl) 0 0;
  gap: var(--gap-5xs);
}
.content {
  flex: 1;
  align-items: flex-end;
  gap: var(--gap-base);
  text-align: center;
  font-size: var(--heading-desktop-h6-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 825px) {
  .price1 {
    font-size: var(--font-size-26xl);
    line-height: 54px;
  }
  .price2 {
    font-size: var(--font-size-7xl);
    line-height: 33px;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--text-regular-normal-size);
    line-height: 22px;
  }
  .price1 {
    font-size: var(--font-size-15xl);
    line-height: 40px;
  }
  .price2 {
    font-size: var(--font-size-lgi);
    line-height: 25px;
  }
  .price {
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
