.iconRelume {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.loremIpsumDolor,
.pageOne {
  align-self: stretch;
  position: relative;
  line-height: 150%;
}
.pageOne {
  font-weight: 600;
}
.loremIpsumDolor {
  font-size: var(--text-small-semi-bold-size);
}
.content,
.menuItem {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.content {
  flex: 1;
  flex-direction: column;
  min-width: 221px;
}
.menuItem {
  align-self: stretch;
  flex-direction: row;
  padding: var(--padding-5xs) 0;
  box-sizing: border-box;
  gap: var(--gap-xs);
  text-align: left;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 450px) {
  .menuItem {
    flex-wrap: wrap;
  }
}
