.divider {
  align-self: stretch;
  height: 1px;
  position: relative;
  background-color: var(--border-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
}
.details {
  flex: 1;
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 43px;
  max-width: 100%;
}
.plusIcon {
  width: 24px;
  height: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.plusWrapper,
.title {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.plusWrapper {
  height: 25.5px;
  flex-direction: column;
  padding: var(--padding-11xs-5) 0 0;
}
.title {
  align-self: stretch;
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-base) 0;
  gap: var(--gap-xs);
  max-width: 100%;
}
.paragraph {
  align-self: stretch;
  position: relative;
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.accordionItem {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
  text-align: left;
  font-size: var(--text-medium-bold-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 825px) {
  .title {
    flex-wrap: wrap;
  }
}
