.subheading {
  width: 67px;
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 67px;
}
.heading {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
}
.content2,
.heading,
.text {
  align-self: stretch;
}
.text {
  position: relative;
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--heading-desktop-h2-size);
}
.sectionTitle {
  flex-direction: column;
  gap: var(--gap-base);
}
.content1,
.list,
.sectionTitle {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.list {
  flex-direction: row;
  padding: var(--padding-5xs) 0;
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--heading-desktop-h6-size);
}
.content1 {
  flex-direction: column;
  gap: var(--gap-13xl);
}
.content,
.placeholderImageIcon {
  flex: 1;
  max-width: 100%;
  min-width: 400px;
}
.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-xl) 0;
  box-sizing: border-box;
  gap: var(--gap-5xl);
}
.placeholderImageIcon {
  height: 640px;
  position: relative;
  overflow: hidden;
  object-fit: cover;
}
.container,
.layout10 {
  align-self: stretch;
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.container {
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  gap: var(--gap-61xl);
}
.layout10 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1200px) {
  .layout10 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 825px) {
  .heading {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .list {
    flex-wrap: wrap;
  }
  .content1 {
    gap: 16px 32px;
  }
  .content,
  .placeholderImageIcon {
    min-width: 100%;
  }
  .container {
    gap: var(--gap-21xl);
  }
  .layout10 {
    gap: 40px 80px;
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .container {
    gap: var(--gap-xl);
  }
  .layout10 {
    gap: 20px 80px;
    padding-top: var(--padding-28xl);
    padding-bottom: var(--padding-28xl);
    box-sizing: border-box;
  }
}
