.loremIpsumDolor,
.mediumLengthHero {
  align-self: stretch;
  position: relative;
}
.mediumLengthHero {
  margin: 0;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
}
.loremIpsumDolor {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.styleprimarySmallfalseDa {
  width: 85px;
}
.actions {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) 0 0;
  gap: var(--gap-base);
}
.column,
.placeholderImageIcon {
  flex: 1;
  max-width: 100%;
  min-width: 400px;
}
.column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.placeholderImageIcon {
  height: 640px;
  position: relative;
  overflow: hidden;
  object-fit: cover;
}
.header1 {
  background-color: var(--color-limegreen);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  gap: var(--gap-61xl);
  text-align: left;
  font-size: var(--heading-desktop-h1-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
.header1,
.product4Parent {
  align-self: stretch;
  max-width: 100%;
}
.dataHub,
.layout1Parent,
.product4Parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.layout1Parent {
  align-self: stretch;
  padding: 0 0 793px;
  box-sizing: border-box;
  max-width: 100%;
}
.dataHub {
  width: 100%;
  position: relative;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .header1 {
    gap: var(--gap-21xl);
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
  .layout1Parent {
    padding-bottom: 335px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .mediumLengthHero {
    font-size: var(--font-size-26xl);
    line-height: 54px;
  }
  .header1 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
  .layout1Parent {
    padding-bottom: 218px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .column,
  .placeholderImageIcon {
    min-width: 100%;
  }
  .header1 {
    gap: var(--gap-xl);
  }
  .layout1Parent {
    padding-bottom: 142px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .mediumLengthHero {
    font-size: var(--font-size-15xl);
    line-height: 40px;
  }
  .header1 {
    padding-top: var(--padding-28xl);
    padding-bottom: var(--padding-28xl);
    box-sizing: border-box;
  }
}
