  line-height: 150%;
}
.textAreaChild,
.textAreaItem {
  width: 2px;
  height: 2px;
  position: absolute;
  margin: 0 !important;
  right: 2px;
  bottom: 1.8px;
  object-fit: contain;
}
.textAreaItem {
  width: 6px;
  height: 6px;
  bottom: 2px;
  z-index: 1;
}
.input2,
.textArea {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.textArea {
  background-color: var(--background-color-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  padding: var(--padding-xs) var(--padding-2xs);
  position: relative;
  min-height: 182px;
  flex-shrink: 0;
  color: var(--color-neutral-neutral);
}
.input2 {
  gap: var(--gap-5xs);
}
.checkbox {
  width: 20px;
  height: 20px;
  position: relative;
  background-color: var(--background-color-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  overflow: hidden;
  flex-shrink: 0;
  debug_commit: f6aba90;
}
.checkboxWrapper {
  height: 19.5px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-11xs-5) 0 0;
  box-sizing: border-box;
}
.label {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 124px;
  flex-shrink: 0;
  debug_commit: f6aba90;
}
.selectedfalse,
.selectedfalseWrapper {
