}
.content {
  width: 420px;
  gap: var(--gap-29xl);
  min-width: 420px;
  max-width: 100%;
  text-align: left;
  font-size: var(--text-medium-bold-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1225px) {
  .content {
    flex: 1;
  }
}
@media screen and (max-width: 750px) {
  .content {
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .container {
    flex-wrap: wrap;
  }
  .content {
    gap: 24px 48px;
  }
}
