.colorDark {
  height: 27px;
  width: 63px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.functionCaller,
.logo {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.logo {
  height: 27px;
  overflow: hidden;
  justify-content: flex-start;
}
.functionCaller {
  align-self: stretch;
  justify-content: center;
  padding: 0 var(--padding-xl);
}
.linkOne,
.linkTwo {
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 66px;
}
.linkTwo {
  min-width: 68px;
}
.linkThree {
  min-width: 121px;
}
.linkFive,
.linkFour,
.linkThree {
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
}
.linkFour {
  width: 98px;
  flex-shrink: 0;
}
.linkFive {
  min-width: 98px;
}
.content,
.errorHandler,
.links {
  display: flex;
  align-items: flex-start;
}
.links {
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.content,
.errorHandler {
  max-width: 100%;
}
.content {
  width: 578px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-13xl);
}
.errorHandler {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
}
.divider {
  align-self: stretch;
  height: 1px;
  position: relative;
  background-color: var(--border-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
}
.relumeAllRights {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 35px;
}
.cookiesSettings,
.privacyPolicy,
.termsOfService {
  position: relative;
  text-decoration: underline;
  line-height: 150%;
}
.cookiesSettings,
.termsOfService {
  display: inline-block;
  min-width: 87px;
}
.cookiesSettings {
  min-width: 105px;
}
.credits,
.footer7,
.footerLinks,
.row {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  max-width: 100%;
}
.credits,
.footer7,
.row {
  align-self: stretch;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.credits,
.footer7 {
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-13xl);
  text-align: left;
  font-size: var(--text-small-semi-bold-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
.footer7 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  padding: var(--padding-61xl) var(--padding-45xl);
  box-sizing: border-box;
  gap: var(--gap-61xl);
  z-index: 1;
  font-size: var(--text-regular-normal-size);
}
@media screen and (max-width: 750px) {
  .links {
    flex-wrap: wrap;
  }
  .content {
    gap: 16px 32px;
  }
  .row {
    flex-wrap: wrap;
  }
  .credits {
    gap: 16px 32px;
  }
  .footer7 {
    gap: 40px 80px;
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .footerLinks {
    flex-wrap: wrap;
  }
  .footer7 {
    gap: 20px 80px;
    padding-top: var(--padding-33xl);
    padding-bottom: var(--padding-33xl);
    box-sizing: border-box;
  }
}
