.heading {
  margin: 0;
  width: 768px;
  position: relative;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.layout242,
.row {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.row {
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 0 var(--padding-9xl);
  gap: var(--gap-29xl);
  font-size: var(--text-regular-normal-size);
}
.layout242 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-93xl) var(--padding-45xl);
  gap: var(--gap-61xl);
  text-align: left;
  font-size: var(--heading-desktop-h3-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .heading {
    font-size: var(--heading-desktop-h4-size);
    line-height: 38px;
  }
}
@media screen and (max-width: 750px) {
  .row {
    gap: var(--gap-5xl);
  }
  .layout242 {
    gap: 40px 80px;
    padding: var(--padding-54xl) var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--heading-desktop-h5-size);
    line-height: 29px;
  }
  .layout242 {
    gap: 20px 80px;
  }
}
