.subheading {
  width: 76px;
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 76px;
}
.function,
.heading {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
}
.function {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content2,
.sectionTitle {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.content2 {
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--heading-desktop-h2-size);
}
.sectionTitle {
  gap: var(--gap-base);
}
.array,
.number {
  align-self: stretch;
  position: relative;
  line-height: 58px;
}
.array {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.listItem {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  min-width: 192px;
}
.number1,
.tree {
  align-self: stretch;
  position: relative;
  line-height: 58px;
}
.tree {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.content1,
.list,
.listItem1 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.listItem1 {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-5xs);
  min-width: 192px;
}
.content1,
.list {
  align-self: stretch;
}
.list {
  flex-direction: row;
  padding: var(--padding-5xs) 0;
  gap: var(--gap-5xl);
  text-align: left;
  font-size: var(--heading-desktop-h2-size);
}
.content1 {
  flex-direction: column;
  gap: var(--gap-13xl);
}
.content,
.placeholderLightboxIcon {
  flex: 1;
  max-width: 100%;
  min-width: 400px;
}
.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-xl) 0;
  box-sizing: border-box;
  gap: var(--gap-5xl);
}
.placeholderLightboxIcon {
  height: 640px;
  position: relative;
  overflow: hidden;
}
.container,
.layout26 {
  align-self: stretch;
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.container {
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  gap: var(--gap-61xl);
}
.layout26 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .heading,
  .number,
  .number1 {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .layout26 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .list {
    flex-wrap: wrap;
  }
  .content1 {
    gap: 16px 32px;
  }
  .content,
  .placeholderLightboxIcon {
    min-width: 100%;
  }
  .container {
    gap: var(--gap-21xl);
  }
  .layout26 {
    gap: 40px 80px;
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading,
  .number,
  .number1 {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .container {
    gap: var(--gap-xl);
  }
  .layout26 {
    gap: 20px 80px;
    padding-top: var(--padding-28xl);
    padding-bottom: var(--padding-28xl);
    box-sizing: border-box;
  }
}
