.placeholderImageIcon {
  align-self: stretch;
  height: 364.8px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.heading,
.text {
  align-self: stretch;
  position: relative;
  line-height: 150%;
}
.heading {
  font-weight: 600;
}
.text {
  font-size: var(--text-small-semi-bold-size);
}
.headingParent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.price {
  align-self: stretch;
  position: relative;
  font-size: var(--heading-desktop-h6-size);
  line-height: 150%;
  font-weight: 600;
  white-space: nowrap;
}
.frameParent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.button {
  position: relative;
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
  font-family: var(--text-medium-bold);
  color: var(--border-primary);
  text-align: left;
  display: inline-block;
  min-width: 81px;
}
.stylesecondarySmalltrueD {
  cursor: pointer;
  border: 1px solid var(--border-primary);
  padding: var(--padding-5xs) var(--padding-xl);
  background-color: transparent;
  align-self: stretch;
  height: 42px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}
.matrixAddition {
  height: 522.8px;
  width: 304px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
  text-align: center;
  font-size: var(--text-medium-bold-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 450px) {
  .price {
    font-size: var(--text-regular-normal-size);
    line-height: 24px;
  }
}
