.dialogButton,
.heading {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  line-height: 58px;
  font-weight: 700;
  font-family: inherit;
}
.dialogButton {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.popover,
.sectionTitle {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.sectionTitle {
  width: 768px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.popover {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
}
.icon {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.button {
  height: 56px;
  width: 56px;
  margin: 0 !important;
  position: absolute;
  top: calc(50% - 28px);
  left: -28px;
  border-radius: var(--br-31xl);
  background-color: var(--background-color-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-base) var(--padding-mini);
}
.content3,
.placeholderImage1 {
  height: 640px;
  position: relative;
}
.content3 {
  width: 1px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.placeholderImage1 {
  align-self: stretch;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.placeholderImage1Wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-13xl) 0 0;
  box-sizing: border-box;
  min-width: 437px;
  max-width: 100%;
}
.icon1,
.placeholderImage2 {
  position: relative;
  overflow: hidden;
}
.placeholderImage2 {
  height: 640px;
  flex: 1;
  max-width: 100%;
  object-fit: cover;
  min-height: 640px;
}
.icon1 {
  height: 24px;
  width: 24px;
  flex-shrink: 0;
}
.button1 {
  height: 56px;
  width: 56px;
  margin: 0 !important;
  position: absolute;
  top: calc(50% - 28px);
  right: -28px;
  border-radius: var(--br-31xl);
  background-color: var(--background-color-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-base) var(--padding-mini);
}
.content4 {
  height: 640px;
  width: 1px;
  position: relative;
}
.content2,
.content4,
.placeholderImage2Parent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.placeholderImage2Parent {
  height: 640px;
  width: 641px;
  max-width: 100%;
}
.content2 {
  width: 1314px;
  min-width: 1314px;
  flex-shrink: 0;
  debug_commit: f6aba90;
}
.placeholderImage3 {
  height: 640px;
  width: 640px;
  position: relative;
  object-fit: cover;
  min-height: 640px;
  max-width: 100%;
}
.content1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-13xl);
  max-width: 100%;
}
.dot,
.dot1,
.dot2,
.dot3,
.dot4 {
  height: 8px;
  width: 8px;
  position: relative;
  border-radius: 50%;
  background-color: var(--border-primary);
}
.dot1,
.dot2,
.dot3,
.dot4 {
  background-color: var(--color-neutral-neutral-lighter);
}
.sliderDots {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-3xs);
  gap: var(--gap-4xs);
}
.content,
.gallery17,
.sliderDotsWrapper {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
}
.sliderDotsWrapper {
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl);
}
.content,
.gallery17 {
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-29xl);
  max-width: 100%;
}
.gallery17 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  gap: var(--gap-61xl);
  text-align: center;
  font-size: var(--heading-desktop-h2-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1425px) {
  .content2 {
    flex: 1;
    min-width: 100%;
  }
  .placeholderImage3 {
    flex: 1;
  }
  .content1 {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 1200px) {
  .content2 {
    flex-wrap: wrap;
    min-width: 100%;
  }
  .gallery17 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 825px) {
  .heading {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .content2,
  .placeholderImage1Wrapper {
    min-width: 100%;
  }
  .content1 {
    gap: 32px 16px;
  }
  .content {
    gap: 24px 48px;
  }
  .gallery17 {
    gap: 40px 80px;
    padding: var(--padding-28xl) var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .content2 {
    min-width: 100%;
  }
  .gallery17 {
    gap: 20px 80px;
  }
}
