.iconRelume {
  width: 48px;
  height: 48px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.heading {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--heading-desktop-h5-size);
  line-height: 140%;
  font-weight: 700;
  font-family: inherit;
}
.button,
.text {
  position: relative;
  line-height: 150%;
}
.text {
  align-self: stretch;
}
.button {
  display: inline-block;
  min-width: 81px;
  white-space: nowrap;
}
.iconChevronRight {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 24px;
}
.stylelinkSmallfalseDark {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.action,
.listItem {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.action {
  align-self: stretch;
  height: 32px;
  padding: var(--padding-5xs) 0 0;
  box-sizing: border-box;
}
.listItem {
  flex: 1;
  gap: var(--gap-5xl);
  min-width: 304px;
  max-width: 100%;
  text-align: left;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-lgi);
    line-height: 27px;
  }
}
