.placeholderImageIcon {
  align-self: stretch;
  height: 480px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.errorHandler {
  height: 21px;
  width: 2px;
  position: relative;
  background-color: var(--border-primary);
}
.imageCaptionGoes {
  flex: 1;
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 121px;
  max-width: 100%;
}
.captionFigure,
.content {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  max-width: 100%;
}
.content {
  flex-direction: column;
  padding: var(--padding-29xl) 0;
  box-sizing: border-box;
  text-align: left;
  font-size: var(--text-small-semi-bold-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 750px) {
  .errorHandler {
    width: 100%;
    height: 2px;
  }
  .captionFigure {
    flex-wrap: wrap;
  }
  .content {
    padding-top: var(--padding-12xl);
    padding-bottom: var(--padding-12xl);
    box-sizing: border-box;
  }
}
