.divider,
.question {
  align-self: stretch;
  position: relative;
}
.divider {
  height: 1px;
  background-color: var(--border-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
}
.question {
  line-height: 150%;
}
.questionWrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-10xs-5) 0 0;
  box-sizing: border-box;
  min-width: 447px;
  max-width: 100%;
}
.icon {
  height: 32px;
  width: 32px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.accordionItem,
.title {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.title {
  align-self: stretch;
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-xl) 0;
  box-sizing: border-box;
  gap: var(--gap-xs);
  max-width: 100%;
}
.accordionItem {
  width: 732px;
  flex-direction: column;
  text-align: left;
  font-size: var(--text-medium-bold-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
