.colorDark {
  height: 27px;
  width: 63px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 27px;
}
.linkOne {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 31px;
}
.linkTwo {
  min-width: 73px;
  white-space: nowrap;
}
.linkFour,
.linkThree,
.linkTwo {
  position: relative;
  line-height: 150%;
  display: inline-block;
}
.linkThree {
  min-width: 66px;
  white-space: nowrap;
}
.linkFour {
  min-width: 37px;
}
.chevronDownIcon {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 24px;
}
.navLinkDropdown {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-9xs);
}
.column,
.content1 {
  display: flex;
  flex-direction: row;
  max-width: 100%;
}
.column {
  height: 24px;
  flex: 1;
  overflow: hidden;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.content1 {
  height: 27px;
  width: 418px;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.button {
  position: relative;
  line-height: 150%;
  display: inline-block;
  min-width: 109px;
  white-space: nowrap;
}
.stylesecondarySmalltrueD {
  border: 1px solid var(--border-primary);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-5xs) var(--padding-lgi);
}
.button1 {
  position: relative;
  line-height: 150%;
}
.actions,
.content,
.styleprimarySmalltrueDar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.styleprimarySmalltrueDar {
  background-color: var(--border-primary);
  border: 1px solid var(--border-primary);
  padding: var(--padding-5xs) var(--padding-lgi);
  white-space: nowrap;
  color: var(--background-color-primary);
}
.actions,
.content {
  gap: var(--gap-base);
  max-width: 100%;
}
.content {
  align-self: stretch;
  border-bottom: 1px solid var(--border-primary);
  box-sizing: border-box;
  overflow: hidden;
  justify-content: space-between;
  padding: var(--padding-base) var(--padding-45xl);
  top: 0;
  z-index: 99;
  position: sticky;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1200px) {
  .content {
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 825px) {
  .column {
    display: none;
  }
  .content1 {
    width: 87px;
  }
}
@media screen and (max-width: 450px) {
  .actions {
    display: none;
  }
}
