.faqs,
.functionSignature {
  align-self: stretch;
  position: relative;
}
.faqs {
  margin: 0;
  font-size: inherit;
  line-height: 58px;
  font-weight: 700;
  font-family: inherit;
}
.functionSignature {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  flex-shrink: 0;
}
.button {
  position: relative;
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
  font-family: var(--text-medium-bold);
  color: var(--border-primary);
  text-align: left;
  display: inline-block;
  min-width: 56px;
}
.sectionTitle,
.stylesecondarySmallfalse {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.stylesecondarySmallfalse {
  cursor: pointer;
  border: 1px solid var(--border-primary);
  padding: var(--padding-xs) var(--padding-5xl);
  background-color: transparent;
  height: 50px;
  box-sizing: border-box;
  flex-direction: row;
}
.sectionTitle {
  width: 500px;
  flex-direction: column;
  gap: var(--gap-13xl);
  min-width: 500px;
  max-width: 100%;
}
.divider {
  width: 732px;
  height: 1px;
  position: relative;
  background-color: var(--border-primary);
  border: 1px solid var(--border-primary);
  box-sizing: border-box;
}
.accordion,
.container,
.faq3 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.accordion {
  flex: 1;
  overflow-x: auto;
  flex-direction: column;
  min-width: 512px;
  font-size: var(--text-medium-bold-size);
}
.container,
.faq3 {
  flex-direction: row;
}
.container {
  flex: 1;
  gap: var(--gap-61xl);
}
.faq3 {
  align-self: stretch;
  background-color: var(--color-royalblue);
  overflow: hidden;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--heading-desktop-h2-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1200px) {
  .sectionTitle {
    flex: 1;
  }
  .container {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1050px) {
  .faqs {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
}
@media screen and (max-width: 750px) {
  .sectionTitle {
    gap: 16px 32px;
  }
  .accordion,
  .sectionTitle {
    min-width: 100%;
  }
  .container,
  .faq3 {
    gap: 80px 40px;
  }
  .faq3 {
    padding: var(--padding-54xl) var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .faqs {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .container,
  .faq3 {
    gap: 80px 20px;
  }
}
