.iconEnvelope {
  width: 48px;
  height: 48px;
  overflow: hidden;
  flex-shrink: 0;
}
.heading,
.iconEnvelope,
.text {
  position: relative;
}
.heading {
  margin: 0;
  align-self: stretch;
  font-size: inherit;
  line-height: 42px;
  font-weight: 700;
  font-family: inherit;
}
.text {
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
}
.content1,
.link,
.text {
  align-self: stretch;
}
.content1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.link {
  position: relative;
  font-size: var(--text-regular-normal-size);
  text-decoration: underline;
  line-height: 150%;
  white-space: nowrap;
}
.contactInfo,
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.contactInfo {
  align-self: stretch;
}
.content {
  flex: 1;
  min-width: 280px;
  max-width: 304px;
  text-align: center;
  font-size: var(--heading-desktop-h4-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1050px) {
  .heading {
    font-size: var(--font-size-7xl);
    line-height: 33px;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-lgi);
    line-height: 25px;
  }
}
