.subheading {
  position: relative;
  line-height: 150%;
  font-weight: 600;
  display: inline-block;
  min-width: 33px;
}
.heading,
.text {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  line-height: 58px;
  font-weight: 700;
  font-family: inherit;
}
.text {
  font-size: var(--text-medium-bold-size);
  line-height: 150%;
}
.content,
.sectionTitle {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.content {
  align-self: stretch;
  align-items: center;
  gap: var(--gap-5xl);
  font-size: var(--heading-desktop-h2-size);
}
.sectionTitle {
  width: 768px;
  align-items: flex-start;
  gap: var(--gap-base);
  max-width: 100%;
}
.button {
  position: relative;
  font-size: var(--text-regular-normal-size);
  line-height: 150%;
  font-family: var(--text-medium-bold);
  color: var(--border-primary);
  text-align: left;
  display: inline-block;
  min-width: 56px;
}
.stylesecondarySmallfalse {
  cursor: pointer;
  border: 1px solid var(--border-primary);
  padding: var(--padding-xs) var(--padding-5xl);
  background-color: transparent;
  height: 50px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.blog44,
.row,
.title {
  align-self: stretch;
  max-width: 100%;
}
.title {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.blog44,
.row {
  align-items: flex-start;
  justify-content: flex-start;
}
.row {
  display: grid;
  flex-direction: row;
  gap: var(--gap-13xl);
  grid-template-columns: repeat(3, minmax(313px, 1fr));
  font-size: var(--text-small-semi-bold-size);
}
.blog44 {
  background-color: var(--background-color-primary);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-93xl) var(--padding-45xl);
  box-sizing: border-box;
  gap: var(--gap-61xl);
  text-align: left;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 1200px) {
  .row {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(313px, 543px));
  }
}
@media screen and (max-width: 1050px) {
  .heading {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .title {
    flex-wrap: wrap;
  }
  .blog44 {
    padding-top: var(--padding-54xl);
    padding-bottom: var(--padding-54xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .row {
    gap: 32px 16px;
    grid-template-columns: minmax(313px, 1fr);
  }
  .blog44 {
    gap: 40px 80px;
    padding: var(--padding-28xl) var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .blog44 {
    gap: 20px 80px;
  }
}
