.iconEnvelope {
  width: 32px;
  height: 32px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.heading,
.link,
.valueComparer {
  align-self: stretch;
  position: relative;
}
.heading {
  font-size: var(--heading-desktop-h6-size);
  line-height: 140%;
}
.link,
.valueComparer {
  line-height: 150%;
  white-space: nowrap;
}
.link {
  text-decoration: underline;
}
.contactInfo,
.content {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.content {
  gap: var(--gap-base);
  text-align: left;
  font-size: var(--text-regular-normal-size);
  color: var(--border-primary);
  font-family: var(--text-medium-bold);
}
@media screen and (max-width: 450px) {
  .heading {
    font-size: var(--text-regular-normal-size);
    line-height: 22px;
  }
}
